import React from "react"
import styled from "styled-components"
import Header from "../components/Header"
import { graphql } from "gatsby"
import "../components/layout.css"

const ProjectTitle = styled.h1`
  font-size: 70px;
  margin: 50px 0;
  text-align: center;
  @media (min-width: 768px) and (max-width: 1024px) {
    font-size: 64px;
  }
  @media (min-width: 200px) and (max-width: 767px) {
    font-size: 46px;
  }
`

const ProjectHolder = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  margin: 20px 0;
`

const ProjectCard = styled.div`
  width: 400px;
  background: #212840;
  overflow: hidden;
  margin: 10px 40px 10px 0;
  border-radius: 2px;

  @media (min-width: 768px) and (max-width: 1024px) {
    margin: 3rem 0 0 0;
  }
  @media (min-width: 200px) and (max-width: 767px) {
    margin: 3rem 0 0 0;
  }
`

const ProjectCardImage = styled.div`
  & img {
    width: 100%;
  }
`

const ProjectCardBody = styled.div`
  padding: 0.6rem 1.5rem;
`

const ProjectCardTitle = styled.p`
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
`

const ProjectCardDescription = styled.p`
  font-size: 14px;
  margin-top: 10px;
  letter-spacing: 1px;
`

const ProjectCardButton = styled.div``

const ProjectCardButtonLinks = styled.button`
  width: 100px;
  height: 40px;
  padding: 0.3rem;
  cursor: pointer;
  color: #fff;
  border-radius: 2px;
  border: none;
  outline: none;
  font-weight: 500;
  margin: 20px 10px 10px 10px;
  letter-spacing: 1px;
  background-color: #313a61;
  font-family: "Ubuntu", sans-serif;
  font-size: 12px;
  -webkit-box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);

  @media (min-width: 200px) and (max-width: 767px) {
    width: 60px;
  }
`

const projects = ({ data }) => {
  const ProjectInfo = data.allProjectsJson.nodes

  return (
    <>
      <Header />

      <div className="wrapper">
        <ProjectTitle>Projects</ProjectTitle>
        <ProjectHolder>
          {ProjectInfo.map(
            ({ name, description, github_url, website_url, image }) => {
              return (
                <ProjectCard key={github_url}>
                  <ProjectCardImage>
                    <a href={website_url}>
                      <img src={image} alt={name} />
                    </a>
                  </ProjectCardImage>
                  <ProjectCardBody>
                    <ProjectCardTitle>{name}</ProjectCardTitle>
                    <ProjectCardDescription>
                      {description}
                    </ProjectCardDescription>
                    <ProjectCardButton>
                      <a href={github_url}>
                        <ProjectCardButtonLinks>
                          View Code
                        </ProjectCardButtonLinks>
                      </a>
                      <a href={website_url}>
                        <ProjectCardButtonLinks>
                          Live Site
                        </ProjectCardButtonLinks>
                      </a>
                    </ProjectCardButton>
                  </ProjectCardBody>
                </ProjectCard>
              )
            }
          )}
        </ProjectHolder>
      </div>
    </>
  )
}

export const query = graphql`
  query MyQuery {
    allProjectsJson {
      nodes {
        name
        description
        github_url
        website_url
        image
      }
    }
  }
`

export default projects
